import Sidebar from "@/components/sidebar";
import Image from "next/image";
import profileDp from "../../public/uploads/profileDp.png";

export default function Downloads() {
  return (
    <>
       <Sidebar/>   
       <aside id="downloads" className=" leftSide fixed top-5 right-2 z-40 w-4/5 bg-white cust_h-56 transition-transform -translate-x-full sm:translate-x-0" aria-label="downloads">
        <div className="download-wrapper">
           <aside className="lt absolute top-0 left-0 w-4/6 h-5/6 transition-transform -translate-x-full sm:translate-x-0">
               <div className="container max-auto">
                <p className="flex text-4xl text-start font-semibold px-4 py-4 hTitle">Daily Downloads
                  <span className="span-container rounded-full">
                    <Image className="rounded-full" src={profileDp} alt="" width={35} height={35}/>
                  </span>
                  <span className="span-container rounded-full">
                    <Image className="rounded-full" src={profileDp} alt="" width={35} height={35}/>
                  </span>
                  <span className="span-container rounded-full">
                    <Image className="rounded-full" src={profileDp} alt="" width={35} height={35}/>
                  </span>
                 
                </p>
               </div>
              
               <div className="">
               <ul role="list" className="p-6 divide-y divide-slate-200">
                
                    
                    <li className="flex py-4 first:pt-0 last:pb-0">
                    <Image className="h-10 w-10 rounded-full" src={profileDp} width={72} height={72} alt="" />
                    <div className="ml-3 overflow-hidden">
                        <p className="text-sm font-medium text-slate-900"></p>
                        <p className="text-sm text-slate-500 truncate"></p>
                    </div>
                    </li>
               
                </ul>
               </div>
           </aside>
           <aside className="lt absolute top-0 right-0">

           </aside>
        </div>
       </aside>
    </>
  );
}
